/**
 * Emi model events
 */

'use strict';

import {EventEmitter} from 'events';
var Emi = require('../../sqldb').Emi;
var EmiEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
EmiEvents.setMaxListeners(0);

// Model events
var events = {
  afterCreate: 'save',
  afterUpdate: 'save',
  afterDestroy: 'remove'
};

// Register the event emitter to the model events
function registerEvents(Emi) {
  for(var e in events) {
    let event = events[e];
    Emi.hook(e, emitEvent(event));
  }
}

function emitEvent(event) {
  return function(doc, options, done) {
    EmiEvents.emit(event + ':' + doc._id, doc);
    EmiEvents.emit(event, doc);
    done(null);
  };
}

registerEvents(Emi);
export default EmiEvents;
